body {
  margin: 0;
}

.bar {
  width: 10px;
}

.bar:not(:last-child) {
  margin-right: 2px;
}

.bars {
  position: absolute;
  left: 50vw;
  top: 50vh;
  transform: translate(-50%, -45%);
  min-height: 600px;
  display: flex;
  align-items: flex-end;
  justify-content: center;
}

.statistics {
  margin-right: 10px;
  min-width: 175px;
}

.statistics p {
  margin: 0;
  font-weight: 700;
  font-size: 18px;
}

p.statistics-comparisons {
  margin: 0;
  margin-bottom: 10px;
}

.controlls {
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 10px 0;
}

.controlls::after {
  content: '';
  position: absolute;
  bottom: 0;
  height: 1px;
  width: 100%;
  background-color: rgba(0, 0, 0, 20%);
}

.controlls-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  width: 100%;
}

.controll {
  background: 0;
  margin: 0;
  padding: 10px 20px;
  border: 1px solid #000;
  border-radius: 30px;
}

.controll:hover {
  cursor: pointer;
  background-color: #FFCF40;
}

.controll:not(:last-child) {
  margin-right: 10px;
}

.logo {
  margin-right: 80px;
}

.logo img {
  height: 50px;
}